import React, { useEffect, useState } from "react";
import { useRecoilValue } from "recoil";
import { selectedAroundFestivalState } from "@states/aroundAtoms";
import { fetchFestivalDetail } from "@services/aroundService";
import { FestivalDetailData } from "../../../types/around";

const FestivalDetail: React.FC = () => {
  console.log("festivalDetail component rendered");
  const selectedFestival = useRecoilValue(selectedAroundFestivalState);
  const [festivalDetail, setfestivalDetail] =
    useState<FestivalDetailData | null>(null);

  useEffect(() => {
    console.log("Selected Festival:", selectedFestival);
    if (selectedFestival && "festivityId" in selectedFestival) {
      fetchFestivalDetail(selectedFestival.festivityId)
        .then((data) => {
          console.log("Fetched Festival Detail:", data);
          setfestivalDetail(data);
        })
        .catch((error) => {
          console.error("Failed to fetch Festival details:", error);
        });
    }
  }, [selectedFestival]);

  useEffect(() => {
    console.log("Festival Detail State:", festivalDetail);
  }, [festivalDetail]);

  if (!festivalDetail) {
    return <div>Loading...</div>;
  }

  return (
    <div>
      <h2>{festivalDetail.contentName}</h2>
      <p>
        <strong>:</strong> {festivalDetail.address}
      </p>
      <p>
        <strong>:</strong> {festivalDetail.contact}
      </p>
      <p>
        <strong>:</strong> {festivalDetail.operationPeriod}
      </p>
      <p>
        <strong>:</strong> {festivalDetail.details}
      </p>
    </div>
  );
};

export default FestivalDetail;
