import React, { useEffect, useState } from "react";
import { useRecoilValue } from "recoil";
import { selectedAroundRestaurantState } from "@states/aroundAtoms";
import { fetchRestaurantDetail } from "@services/aroundService";
import { RestaurantDetailData } from "../../../types/around";

const RestaurantDetail: React.FC = () => {
  console.log("RestaurantDetail component rendered");
  const selectedRestaurant = useRecoilValue(selectedAroundRestaurantState);
  const [restaurantDetail, setRestaurantDetail] =
    useState<RestaurantDetailData | null>(null);

  useEffect(() => {
    console.log("Selected Restaurant:", selectedRestaurant);
    if (selectedRestaurant && "restaurantId" in selectedRestaurant) {
      fetchRestaurantDetail(selectedRestaurant.restaurantId)
        .then((data) => {
          console.log("Fetched Restaurant Detail:", data);
          setRestaurantDetail(data);
        })
        .catch((error) => {
          console.error("Failed to fetch restaurant details:", error);
        });
    }
  }, [selectedRestaurant]);

  useEffect(() => {
    console.log("Restaurant Detail State:", restaurantDetail);
  }, [restaurantDetail]);

  if (!restaurantDetail) {
    return <div>Loading...</div>;
  }

  return (
    <div>
      <h2>{restaurantDetail.restaurantName}</h2>
      <p>
        <strong>:</strong> {restaurantDetail.address}
      </p>
      <p>
        <strong>:</strong> {restaurantDetail.contact}
      </p>
      <p>
        <strong>:</strong> {restaurantDetail.operatingHours}
      </p>
      <p>
        <strong>:</strong> {restaurantDetail.details}
      </p>
      {/*      */}
    </div>
  );
};

export default RestaurantDetail;
